import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { get } from '../utils/httpClient';
//import { Loading } from '../components/Loading';
import placeholderImg from "../placeholder.jpg";
import logo_placeholderImg from "../logo_placeholder.png";
import { topFunction } from '../';
import styles from "./CardDetail.module.css";
import { Card } from '../components/Card';
//import movie from "./movie_ex.json";
import { SlArrowRight } from 'react-icons/sl';

export function CardDetail(){

    const { cardId } = useParams();
    const [isLoading, setIsLoading] = useState(true);
    const [card, setCard] = useState(null);
    const [cardSimilars, setCardSimilars] = useState([]);

    useEffect(() => {
        setIsLoading(true);

        get("/movie/" + cardId).then((data) => {

            setIsLoading(false);
            setCard(data);
            topFunction();

        });

        get("/movie/" + cardId +"/similar").then((dataS) => {
            setCardSimilars(dataS.results);

        });
    }, [cardId]);

    /* if(isLoading){ return <Loading /> } */

    if(!card){ return null; }

    const imagenBcURL = card.backdrop_path ? "https://image.tmdb.org/t/p/w1280" + card.backdrop_path : placeholderImg;
    const imagenLgURL = card.logo_path ? "https://image.tmdb.org/t/p/w500" + card.backdrop_path : logo_placeholderImg;
    
    return(
        <>
        <header className={styles.header}>
            <div className={styles.movie_info}>
                <p className={styles.movie_info__category}>Pelcula</p>
                <div className={styles.movie_info__logo_container}>
                    <img className={styles.movie_info__logo} src={imagenLgURL} alt={card.title} />
                </div>
                <div className={styles.movie_info__buttons}>
                    <button className={styles.movie_info__button+ ' ' + styles.button___play}>Reproducir</button>
                </div>
            </div>
            <div className={styles.movie_preview}>
                <img src={imagenBcURL} alt={card.title} />
            </div>
        </header>
        <section>
            <div className={styles.card_info}>
                <div className={styles.card_info__column}>
                    <div className={styles.card_info__year_duration}><span>{card.release_date}</span>{" "}<span>{card.runtime + "min"}</span></div>
                    <div className={styles.card_info__description}>
                        <p>{card.overview}</p>
                    </div>
                </div>
                <div className={styles.card_info__column}>
                    <div className={styles.more_info__content}>                        
                        <dl>
                            <dt>
                                <span>Direccin</span>
                            </dt>
                            <dd>
                                <p>Lorem ipsum</p>
                            </dd>
                        </dl>
                        <dl>
                            <dt>
                                <span>Reparto</span>
                            </dt>
                            <dd>
                                <p>Dolor, sit amet, consectetur, adipisicing elit, lorem ipsum</p>
                            </dd>
                        </dl>
                        <dl>
                            <dt>
                                <span>Gneros</span>
                            </dt>
                            <dd>
                                <p>{card.genres.map((genre) => genre.name).join(", ")}</p>
                            </dd>
                        </dl>
                        <dl>
                            <dt>
                                <span>Productores</span>
                            </dt>
                            <dd>
                                <p>
                                    {card.production_companies.map((companies) => companies.name).join(", ")}
                                </p>
                            </dd>
                        </dl>
                        <dl>
                            <dt>
                                <span>Estudio</span>
                            </dt>
                            <dd>
                                <p>Consectetur</p>
                            </dd>
                        </dl>
                    </div>
                </div>
            </div>
            <div className={styles.lists_content}>
                <div className={styles.list_content} data-list_content="new">
                    <h3 className={styles.list_content__section}>Similares</h3>

                    <div className={styles.list_content__cards_container}>
                        <span className={styles.lists_content__arrow+ ' ' + styles.arrow___prev}><SlArrowRight /></span>
                        <ul className={styles.list_content__cards}>

                            {cardSimilars.map((cardS) => {
                                return( <Card card={cardS} link={"detail"}></Card> );

                            })}

                        </ul>
                        <span className={styles.lists_content__arrow+ ' ' + styles.arrow___next}><SlArrowRight /></span>
                    </div>
                </div>
            </div>  
        </section>
        </>
    )
}