import { useEffect, useState } from 'react';
import { get } from '../utils/httpClient';
import { Card } from "./Card";
import './ListsContent.css';
import { Loading } from './Loading';
import { SlArrowRight } from 'react-icons/sl';


export function ListsContent({content, typeContent}){
    const [isLoading, setIsLoading] = useState(true);
    const [cards, setCards] = useState([]);
    const [genre, setGenre] = useState("");

    useEffect(() => {
        setIsLoading(true);

        const linkContent = content.linkDb;

        if(content.type === "genre"){
            get("/genre/"+typeContent+"/list").then((dataGenre) => {

                var randomGenre = dataGenre.genres[Math.floor(Math.random()*dataGenre.genres.length)];
                setGenre(randomGenre.name);
                get("/discover/"+typeContent+linkContent+randomGenre.id).then((data) => {
                    setIsLoading(false);                    
                    setCards(data.results);
                });
                
            });
        }else{
            get("/discover/"+typeContent+linkContent).then((data) => {
                setIsLoading(false);
                setCards(data.results);
            });
        }
        
    }, []);

    /* if(isLoading){ return <Loading /> } */
    
    return (
        <>
            <div className={ "list_content list_content--" + content.typeList } data-list_content={content.type}>

                <h3 className="list_content__section">{content.type === "genre" ? "Genero: "+genre : content.headTitle}</h3>

                <div className="list_content__cards_container">
                    <span className="lists_content__arrow arrow--prev"><SlArrowRight /></span>
                    <ul className="list_content__cards">

                        {cards.map((cardContent) => {
                            /* if(!cardContent.backdrop_path){ return null }else{
                                return (<Card key={cardContent.id} card={cardContent}></Card>)
                            } */

                            return (<Card key={cardContent.id} card={cardContent} link={"search"}></Card>)
                            
                        })}

                    </ul>
                    <span className="lists_content__arrow arrow--next"><SlArrowRight /></span>
                </div>
                
            </div>
        </>
    )
}