//import { useState } from "react";
import { useState } from "react";
import { useHistory, } from "react-router-dom";
//import { useQuery } from "../hooks/useQuery";
import { BiSearch } from 'react-icons/bi';
import './SearchInput.css';



export function SearchInput(){
    //const [searchValue, setSearchValue] = useState("");
    const history = useHistory();

    //const query = useQuery();
    //const search = query.get("search");

    const [link, setLink] = useState("");

    return(
        <>
            <div className="menu__search_container" id="menu__search_container">                
                <label for="menu__search"><span className="menu__search_icon"><BiSearch size={2+"rem"} /></span></label>
                <input id="menu__search" className="menu__search"
                type="search"
                value={link}
                placeholder="Search"
                onChange={(e) => {

                    const value = e.target.value;
                    value === "" ? history.push("") : history.push("/search/" + value);                
                    setLink(value);
                    
                }}></input>
            </div>
        </>
    )
}