import { Link } from 'react-router-dom';
import styles from './Card.module.css';
import placeholderImg from "../placeholder.jpg";

export function Card({card, link}){

    const imagenURL = card.backdrop_path ? "https://image.tmdb.org/t/p/w300" + card.backdrop_path : placeholderImg;
    var linkPath = "card_detail/" + card.id;

    if(link === "detail"){
        linkPath = "../../card_detail/" + card.id;
    }else if(link === "search"){
        linkPath = "../card_detail/" + card.id;
    }

    return (        
        <li className={styles.card}>
            <Link to={linkPath}>
                <div className={styles.card__img_container}>
                    <img className={styles.card__img} src={imagenURL} alt={card.title}></img>
                </div>
            </Link>
        </li>
    );
}