import ListContent  from "./ListContent.json";
import logo_movie from "../logo_movie.png";
import bg_movie from "../punales.jpg";
import { ListsContent } from "../components/ListsContent";

export function Home({typeContent}){
    return(
        <>
        <header className="header">
            <div className="movie_info">
                <p className="movie_info__category">Pelcula</p>
                <div className="movie_info__logo_container">
                    <img className="movie_info__logo" src={logo_movie} alt="Logotipo de..."></img>
                </div>
                <p className="movie_info__description">Un detective resuelve la intricada red de secretos y mentiras que rodean la muerte de un exitoso escritor de novelas de misterio y a su inquietante y excntrica familia.</p>
                <div className="movie_info__buttons">
                    <button className="movie_info__button button--play">Reproducir</button>
                    <button className="movie_info__button button--info">Ms informacin</button>
                </div>
            </div>
            <div className="movie_preview">
                <img src={bg_movie} alt=""></img>
            </div>
        </header>

        <section className="lists_content">

            {ListContent.map((content) => {
                return (<ListsContent key={content.id} content={content} typeContent={typeContent}></ListsContent>)
                
            })}

        </section>
        </>
    );
}