import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Card } from "../components/Card";
//import { Loading } from "../components/Loading";
//import { useQuery } from "../hooks/useQuery";
import { get } from "../utils/httpClient";
import styles from "./Search.module.css"

export function Search(){

    const { searchLink } = useParams();
    
    //const query = useQuery();
    //const search = query.get("search");

    //const [isLoading, setIsLoading] = useState(true);
    const [cards, setCards] = useState([]);

    useEffect(() => {
        //setIsLoading(true);

        get("/search/movie?query=" + searchLink).then((data) => {

            //setIsLoading(false);
            setCards(data.results);
            
        });
    }, [searchLink]);

    /* if(isLoading){ return <Loading /> } */

    return(
        <>
        <section className={styles.lists_content}>
            <div className={styles.list_content +" "+ styles.list_content___double_row} data-list_content="new">

                <h3 className={styles.list_content__section}>{"Search: " + searchLink}</h3>

                <div className={styles.list_content__cards_container}>
                    <span className={styles.lists_content__arrow +" "+ styles.arrow___prev}><img src="../resources/img/arrow.png" alt=""/></span>
                    <ul className={styles.list_content__cards}>

                        {cards.map((cardContent) => {

                            return (<Card key={cardContent.id} card={cardContent} link={"search"}></Card>)
                            
                        })}

                    </ul>
                    <span className={styles.lists_content__arrow +" "+ styles.arrow___next}><img src="../resources/img/arrow.png" alt=""/></span>
                </div>
                
            </div>
        </section>
        </>
    )
}