import "./App.css";
import { Home } from "./pages/Home";
import { Search } from "./pages/Search";
import { CardDetail } from "./pages/CardDetail";
import { Switch, Route, Link } from "react-router-dom";
import { SearchInput } from "./components/SearchInput";
import logo from "./logo.png";


export function App() {    

    return(
        <main>

            <nav className="menu">
                <div className="menu__logo">
                    <Link to=""><img src={logo} alt="Logo de Prinet+"></img></Link>
                </div>

                <ul className="menu__options">
                    <li className="menu__option"><Link to="">Inicio</Link></li>
                    <li className="menu__option"><Link to="/movies">Pelculas</Link></li>
                    <li className="menu__option"><Link to="/series">Sries</Link></li>
                    {/* <li className="menu__option"><Link to="/my_list">Mi lista</Link></li> */}
                    
                </ul>
                <SearchInput />
                <div className="menu__user">
                    {/* <img className="menu__user_img" src="" alt=""></img> */}
                </div>
            </nav>

            <Switch>
                <Route path="/card_detail/:cardId"> <CardDetail /> </Route>                
                {/* <Route path="/my_list"> <MyListContent /> </Route> */}
                <Route path="/series"> <Home key={"tv"} typeContent={"tv"}/>  </Route>
                <Route path="/movies"> <Home key={"movie"} typeContent={"movie"}/> </Route>
                <Route path="/search/:searchLink"> <Search /> </Route>
                <Route path=""> <Home key={"intro"} typeContent={"movie"}/> </Route>
                
            </Switch>

            <footer className="footer">
                <div className="footer__logo">
                    <img src={logo} alt="Logo de Prinet+"></img>
                </div>
                <ul className="footer__links">
                    <li className="footer__link">Trminos y Aviso de privacidad</li>
                    <li className="footer__link">Envanos tus comentarios</li>
                    <li className="footer__link">Ayuda</li>
                    <li className="footer__link">Aviso sobre cookies</li>
                </ul>
                <p className="foooter__text"> Prinet. Todos los derechos reservados</p>
            </footer>

        </main>
    );
}